import React from "react";
import sound from "../sounds/mixkit-message-pop-alert-2354.mp3";
import { Howl } from "howler";
import equals from "../sounds/mixkit-hard-pop-click-2364.wav";
import divide from "../images/Divide.png";
import back from "../images/Backspace (2).png";
import multiply from "../images/X.png";
import minus from "../images/Minus.png";
import plus from "../images/Plus.png";
import cancelAll from "../images/C.png";
import equalTo from "../images/Equals.png";
// import back from "./images/Backspace.png"
// import back from "./images/Divide.png"

export default function Buttons() {
  const callMySound = (src) => {
    const sound = new Howl({ src, html5: true });
    sound.play();
  };

  let expression;
  let inputDisplay;
  let result;

  document.addEventListener("keypress", (event) => {
    press(event);
  });
  //  function pressOperation(event) {
  //     callMySound();

  //  }
  function press(event) {
    //  if (event.target.className == "coloured") {
    //    callMySound(equals);
    //  } else {
    //    callMySound(sound);
    //  }
    callMySound(sound);
    //  callMySound(sound);
    //  alert(event.target.className);
    let attribute = event.target.dataset.symbol;
    attribute = event.target.getAttribute("data-symbol");
    console.log("Data Attribute: ", attribute);
    console.log("Event Id: ", event.target.id);
    inputDisplay = document.querySelector(".input");

    let lastValue =
      inputDisplay.textContent[inputDisplay.textContent.length - 1];
    console.log(lastValue);
    if (
      (event.target.id == "*" || event.target.id == "/") &&
      event.target.id == lastValue
    ) {
      console.log("hello");
      // del()
      return;
    }
    inputDisplay.textContent += event.target.id;

    expression = inputDisplay.value;
  }

  function getResult() {
    callMySound(equals);
    result = document.querySelector(".output");

    if (inputDisplay == undefined) {
      result.innerText = "0";
    }
    result.innerText = eval(expression);
  }
  function del() {
    callMySound(equals);
    inputDisplay.textContent = inputDisplay.textContent.slice(
      0,
      inputDisplay.textContent.length - 1
    );
    expression = expression.slice(0, expression.length - 1);
  }

  function cancel() {
    callMySound(equals);
    inputDisplay.textContent = null;
    expression = null;
    result.innerText = null;
  }
  return (
    <div className="Buttons">
      <div className="buttonContainer">
        <button onClick={cancel} className="coloured">
          {" "}
          C{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="**" onClick={press} className="coloured">
          {" "}
          **{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="del" onClick={del} className="coloured">
          {/* &lt;&lt;  */}
          <img src={back}></img>
        </button>
      </div>
      <div className="buttonContainer">
        <button id="/" onClick={press} className="coloured">
          {" "}
          /{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="7" className="seven" onClick={press}>
          {" "}
          7{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="8" onClick={press}>
          {" "}
          8{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="9" onClick={press}>
          {" "}
          9
        </button>
      </div>
      <div className="buttonContainer">
        <button id="*" onClick={press} className="coloured">
          {/* <img src={multiply}></img>  */}
          &lowast;
        </button>
      </div>
      <div className="buttonContainer">
        <button id="4" onClick={press}>
          {" "}
          4
        </button>
      </div>
      <div className="buttonContainer">
        <button id="5" onClick={press}>
          {" "}
          5{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="6" onClick={press}>
          {" "}
          6{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button
          id="-"
          data-symbol="minusAttribute"
          onClick={press}
          className="coloured"
        >
          {/* <img src={minus}></img>  */}-
        </button>
      </div>
      <div className="buttonContainer">
        <button id="1" onClick={press}>
          {" "}
          1{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="2" onClick={press}>
          {" "}
          2{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="3" onClick={press}>
          {" "}
          3
        </button>
      </div>
      <div className="buttonContainer">
        <button id="+" onClick={press} className="coloured">
          {/* <img src={plus}></img> */}+
        </button>
      </div>
      <div className="buttonContainer">
        <button id="00" onClick={press}>
          {" "}
          00{" "}
        </button>
      </div>
      <div className="buttonContainer">
        <button id="0" onClick={press}>
          {" "}
          0
        </button>
      </div>
      <div className="buttonContainer">
        <button id="." onClick={press}>
          {" "}
          .
        </button>
      </div>
      <div className="buttonContainer">
        <button onClick={getResult} className="coloured">
          {" "}
          <img src={equalTo}></img>{" "}
        </button>
      </div>
    </div>
  );
}
